/* TODO: Slide left/right https://tympanus.net/Development/PageTransitions/ */

.fade-left--enter {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-45%);
      -ms-transform: translateX(-45%);
          transform: translateX(-45%);
  opacity: 0;
}

.fade-left--enter-active {
  -webkit-transition: opacity 0.5s ease-out 0.7s, -webkit-transform 0.5s ease-out 0.7s;
  transition: opacity 0.5s ease-out 0.7s, -webkit-transform 0.5s ease-out 0.7s;
  -o-transition: opacity 0.5s ease-out 0.7s, transform 0.5s ease-out 0.7s;
  transition: opacity 0.5s ease-out 0.7s, transform 0.5s ease-out 0.7s;
  transition: opacity 0.5s ease-out 0.7s, transform 0.5s ease-out 0.7s, -webkit-transform 0.5s ease-out 0.7s;
  opacity: 1;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.fade-left--exit {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 1;
  pointer-events: none; /* Stop interactivity on fading page */
}

.fade-left--exit-active {
  -webkit-transition: opacity 0.5s ease-in, -webkit-transform 0.5s ease-out;
  transition: opacity 0.5s ease-in, -webkit-transform 0.5s ease-out;
  -o-transition: opacity 0.5s ease-in, transform 0.5s ease-out;
  transition: opacity 0.5s ease-in, transform 0.5s ease-out;
  transition: opacity 0.5s ease-in, transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
  opacity: 0;
  -webkit-transform: translateX(-55%);
      -ms-transform: translateX(-55%);
          transform: translateX(-55%);
}

.fade-zoom--enter {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 0) scale(0.9);
      -ms-transform: translate(-50%, 0) scale(0.9);
          transform: translate(-50%, 0) scale(0.9);
  opacity: 0;
}

.fade-zoom--enter-active {
  -webkit-transition: opacity 0.5s ease-out 0.7s, -webkit-transform 0.5s ease-out 0.7s;
  transition: opacity 0.5s ease-out 0.7s, -webkit-transform 0.5s ease-out 0.7s;
  -o-transition: opacity 0.5s ease-out 0.7s, transform 0.5s ease-out 0.7s;
  transition: opacity 0.5s ease-out 0.7s, transform 0.5s ease-out 0.7s;
  transition: opacity 0.5s ease-out 0.7s, transform 0.5s ease-out 0.7s, -webkit-transform 0.5s ease-out 0.7s;
  opacity: 1;
  -webkit-transform: translate(-50%, 0) scale(1);
      -ms-transform: translate(-50%, 0) scale(1);
          transform: translate(-50%, 0) scale(1);
}

.fade-zoom--exit {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%) scale(1);
      -ms-transform: translateX(-50%) scale(1);
          transform: translateX(-50%) scale(1);
  opacity: 1;
  pointer-events: none; /* Stop interactivity on fading page */
}

.fade-zoom--exit-active {
  -webkit-transition: opacity 0.5s ease-in, -webkit-transform 0.5s ease-in;
  transition: opacity 0.5s ease-in, -webkit-transform 0.5s ease-in;
  -o-transition: opacity 0.5s ease-in, transform 0.5s ease-in;
  transition: opacity 0.5s ease-in, transform 0.5s ease-in;
  transition: opacity 0.5s ease-in, transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
  opacity: 0;
  -webkit-transform: translateX(-50%) scale(1.1);
      -ms-transform: translateX(-50%) scale(1.1);
          transform: translateX(-50%) scale(1.1);
}



