.cart {
  display: -ms-flexbox;
  display: flex;
  color: black;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.cart__item {
  position: relative;
  cursor: pointer;
  width: 20%;
  background-color: white;
  padding: 1rem;
  text-align: center;
  font-size: 1.4rem;
  line-height: 1;
  -ms-flex-positive: 0;
      flex-grow: 0;
  margin: 1rem;
  border: 5px solid white;
  flex-grow: 0;
  -webkit-transition: border 0.2s ease, -webkit-box-shadow 0.2s ease, -webkit-transform 0.2s ease;
  transition: border 0.2s ease, -webkit-box-shadow 0.2s ease, -webkit-transform 0.2s ease;
  -o-transition: border 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
  transition: border 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
  transition: border 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease, -webkit-box-shadow 0.2s ease, -webkit-transform 0.2s ease;
  -webkit-box-shadow: 0.3rem 0.3rem 0 rgba(35, 117, 83, 0.42);
          box-shadow: 0.3rem 0.3rem 0 rgba(35, 117, 83, 0.42);
}

.cart__item:hover {
  -webkit-box-shadow: 0.5rem 0.5rem 0 rgba(35, 117, 83, 0.42);
          box-shadow: 0.5rem 0.5rem 0 rgba(35, 117, 83, 0.42);
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.cart__item--selected {
  border: 5px solid #37bf86;
}

.cart__item-image {
  width: 100%;
}

.cart__item-button {
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
  width: 1.3rem;
  cursor: pointer;
}

