.bar {
  position: relative;
  background-color: white;
  height: 1rem;
  width: 100%;
  margin-bottom: 4rem;
}

.bar__inner {
  position: absolute;
  background-color: #37bf86;
  height: 100%;
  width: 0;
  -webkit-transition: width 0.25s ease;
  -o-transition: width 0.25s ease;
  transition: width 0.25s ease;
}

.bar__label-container {
  position: absolute;
  top: 100%;
  left: 0;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  text-align: center;
  margin-top: 0.5rem;
  -webkit-transition: left 0.25s ease;
  -o-transition: left 0.25s ease;
  transition: left 0.25s ease;
}

.bar__label {
  color: white;
  font-size: 2rem;
}

.bar__arrow {
  margin: 0 auto;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0.75rem 0.75rem 0.75rem;
  border-color: transparent transparent #37bf86 transparent;
}



