.score-display {
  color: white;
  line-height: 0.95;
  width: 12rem;
}

.score-display__label {
  font-size: 1.2rem;
}

.score-display__value-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  font-size: 3rem;
}

.score-display__value-container--right {
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.score-display__value {
  display: inline-block;
  position: relative;
}

.score-display__icon {
  display: inline-block;
  width: 2.3rem;
  height: 2.3rem;
  margin-right: 0.3rem;
}

.score-display__toast {
  position: absolute;
  top: 100%;
  left: 0;
}

.score-display__value-container--right .score-display__toast {
  left: initial;
  right: 0;
}



