.browser {
  width: 100%;
  height: 100%;
  height: 75vh;
  background-color: #d6d6d6;
  border-radius: 1rem;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding: 0 0.3rem 0.3rem 0.3rem;
}

.browser-bar {
  height: 3rem;
  background-color: #d6d6d6;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 0;
      flex-grow: 0;
}

.browser-button {
  height: 1rem;
  width: 1rem;
  margin-left: 0.6rem;
  border-radius: 50%;
}

.browser-button:nth-child(1) {
  background-color: #fa6059;
  margin-left: 1rem;
}

.browser-button:nth-child(2) {
  background-color: #fcbc32;
}

.browser-button:nth-child(3) {
  background-color: #32c942;
}

.browser-content {
  background-color: #229452;
  width: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  border-radius: 0 0 1rem 1rem;
}

.website {
  height: 100%;
}

.website__banner {
  background-color: #27ae60;
  height: 2rem;
  text-align: center;
}

.website__content {
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: white;
}



