.tooltip {
  cursor: pointer;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-size: 1.5rem;
}

.tooltip__container {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%, -100%);
      -ms-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  min-width: 35vw;
