.tooltip__content {
  background-color: #37bf86;
  border-radius: 1rem;
  color: white;
  padding: 2rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 400;
}

.tooltip__content::after {
  /* Triangle */
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
      -ms-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 1rem 1rem 0 1rem;
  border-color: #37bf86 transparent transparent transparent;
