@import "./reset.css";
@import "./typography.css";
@import "./general-layout.css";
@import "./grid.css";
@import "./page-transitions.css";
@import "./hud.css";

#root {
  position: relative;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

body {
  background: url("../images/background/doodles-background.png") repeat;
}

.button {
  font-size: 1.2rem;
  font-weight: 700;
  color: white;
  text-transform: uppercase;
  display: inline-block;
  padding: 0.75rem 2.25rem;
  border: 0.25rem solid white;
  margin: 0.5rem;
  letter-spacing: 0.1rem;
  -webkit-transition: background-color 0.25s ease;
  -o-transition: background-color 0.25s ease;
  transition: background-color 0.25s ease;
  cursor: pointer;
  background-color: #2d87d8;
}

.button:hover {
  background-color: #051a2d;
}



