.container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 0.5rem 2rem 0 2rem;
}

@media (min-width: 950px) {
  .container {
    padding: 0;
  }
}

.fullwidth {
  width: 100%;
}

.page-wrapper {
  width: 100%;
  position: relative;
}

.page {
  width: 100%;
  position: relative;
}

.fullscreen-container {
  min-height: 100vh;
  min-width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}



